function sliderAccount() {
    var swiper = new Swiper('.account .swiper', {
        slidesPerView: 2.4,
        spaceBetween: 20,
        breakpoints: {
            320: {
                slidesPerView: 1.5,
                spaceBetween: 15,
            },
            401: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            577: {
                slidesPerView: 2.4,
                spaceBetween: 20,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            1201: {
                slidesPerView: 2.4,
                spaceBetween: 20,
            }
        }
    })
}

function sliderQuiz() {
    var swiper = new Swiper('.quiz .swiper', {
        spaceBetween: 20,
        effect: "fade",
        allowTouchMove: false,
        navigation: {
            nextEl: ".quiz .swiper-button-next",
            prevEl: ".quiz .swiper-button-prev",
        },
    })
}

$(document).ready(function() {

    sliderAccount();
    sliderQuiz();

    //Burger-Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__list').toggleClass('header__list--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Select
    $(".custom_select select").niceSelect()


})